const actions = {
  getItemRequests: (page, search, filter) => ({
    type: "GET_ITEM_REQUESTS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  getItems: (id_location, search) => ({
    type: "GET_FILTER_ITEMS_RQ_REQUEST",
    payload: { id_location: id_location, search: search }
  }),
  createItemRequest: () => ({
    type: "CREATE_ITEM_REQUEST_REQUEST"
  }),
  getTransactions: (page, search, filter) => ({
    type: "GET_TRANSACTIONS_REQUEST",
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  storeItemRequest: data => ({
    type: "STORE_ITEM_REQUEST_REQUEST",
    payload: { body: data }
  }),
  editItemRequest: id_item_request => ({
    type: "EDIT_ITEM_REQUEST_REQUEST",
    payload: { id_item_request: id_item_request }
  }),
  updateItemRequest: (id_item_request, data) => ({
    type: "UPDATE_ITEM_REQUEST_REQUEST",
    payload: {
      id_item_request: id_item_request,
      body: data
    }
  }),
  showItemRequest: id_item_request => ({
    type: "SHOW_ITEM_REQUEST_REQUEST",
    payload: { id_item_request: id_item_request }
  }),
  confirmItemRequest: id_item_request => ({
    type: "CONFIRM_ITEM_REQUEST_REQUEST",
    payload: { id_item_request }
  }),
  closeItemRequest: (id_item_request, data) => ({
    type: "CLOSE_ITEM_REQUEST_REQUEST",
    payload: { id_item_request, body: data }
  }),
  uploadDocument: (id_item_request, data) => ({
    type: "UPLOAD_ITEM_REQUEST_DOCUMENT_REQUEST",
    payload: {
      id_item_request,
      body: data
    }
  }),
  downloadDocument: id_item_request_document => ({
    type: "DOWNLOAD_ITEM_REQUEST_DOCUMENT_REQUEST",
    payload: { id_item_request_document }
  }),
  deleteDocument: (id_item_request_document, id_item_request) => ({
    type: "DELETE_ITEM_REQUEST_DOCUMENT_REQUEST",
    payload: {
      id_item_request_document: id_item_request_document,
      id_item_request: id_item_request
    }
  }),
  setPrintData: data => ({
    type: "SET_PRINT_DATA_ITEM_REQUEST",
    payload: { body: data }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
