import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";
import moment from "moment";

import notification from "../../../../components/notification";
import {
  Empty,
  Input,
  Tooltip,
  Collapse,
  Select,
  DatePicker,
  Row,
  Col
} from "antd";

// import Popconfirms from "../../../../components/feedback/popconfirm";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/inventory/itemRequest/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, {
  Box,
  StatusTag,
  StatusBadge,
  TransNo,
  customCollapseStyle
} from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

const Panel = Collapse.Panel;
const Option = Select.Option;
const Search = Input.Search;
const { RangePicker } = DatePicker;

class ItemRequest extends Component {
  state = {
    filter: {
      id_supplier: null,
      id_location: null,
      transaction_status: null,
      start_date: null,
      end_date: null,
      pagination: null
    },
    search: null
  };

  // Get All data diawal
  componentDidMount() {
    const { getItemRequests } = this.props;
    getItemRequests();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.itemRequest.error) {
      notification("error", this.props.itemRequest.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.itemRequest.success) {
      notification("success", this.props.itemRequest.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    const { getItemRequests } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        transaction_status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    getItemRequests(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;
    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getItemRequests } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        transaction_status: null,
        start_date: null,
        end_date: null,
        pagination: null
      },
      search: null
    });
    getItemRequests();
  };

  //   Handle filter change
  handleFilterChange = (name, value) => {
    if (name === "date" && value[0] && value[1]) {
      const start = value[0].format("YYYY-MM-DD HH:mm:ss");
      const end = value[1].format("YYYY-MM-DD HH:mm:ss");
      // console.log(start, end);
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: start,
          end_date: end
        }
      });
    } else if (name === "date" && !value[0] && !value[1]) {
      this.setState({
        filter: {
          ...this.state.filter,
          start_date: null,
          end_date: null
        }
      });
    } else {
      this.setState({
        filter: {
          ...this.state.filter,
          [name]: value
        }
      });
    }
  };

  // Handle filter
  handleFilter = () => {
    const { getItemRequests } = this.props;
    //const { current_page } = this.props.itemRequest;
    const { filter } = this.state;
    let string = "";

    if (filter.id_location) {
      string += "id_location=" + filter.id_location;
    }
    if (filter.transaction_status) {
      string
        ? (string += "&transaction_status=" + filter.transaction_status)
        : (string += "transaction_status=" + filter.transaction_status);
    }
    if (filter.start_date && filter.end_date) {
      string
        ? (string +=
            "&start_date=" + filter.start_date + "&end_date=" + filter.end_date)
        : (string +=
            "start_date=" + filter.start_date + "&end_date=" + filter.end_date);
    }
    if (filter.id_supplier) {
      string
        ? (string += "&id_supplier=" + filter.id_supplier)
        : (string += "id_supplier=" + filter.id_supplier);
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getItemRequests(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getItemRequests } = this.props;
    this.setState({
      filter: {
        id_supplier: null,
        id_location: null,
        transaction_status: null,
        start_date: null,
        end_date: null,
        pagination: null
      }
    });
    this.setState({ search: null });
    getItemRequests();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateItemRequest(id);
    } else {
      this.props.deactivateItemRequest(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getItemRequests } = this.props;
    const { search, filter } = this.props.itemRequest;
    getItemRequests(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_item_request",
    rowKey: "id_item_request",
    width: "10%",
    align: "center",
    fixed: "right",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>
      </div>
    )
  };

  render() {
    // console.log(this.props);

    // Data setup
    const { itemRequests, locations } = this.props.itemRequest;
    //console.log(this.props.itemRequest); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    const { loading, loadingForeign } = this.props.itemRequest;

    // Definisi kolom2 serta field2 API yang dipakai tabel
    const columns = [
      {
        title: getLanguage({ id: "transactionNo" }),
        dataIndex: "transaction_no",
        rowKey: "transaction_no",
        width: "30%",
        render: (text, row) => {
          let className;
          switch (row.transaction_status) {
            case 1:
              className = "new";
              break;
            case 2:
              className = "requested";
              break;
            case 3:
              className = "progress";
              break;
            case 4:
              className = "fulfilled";
              break;
            case 5:
              className = "cancelled";
              break;
            default:
              className = "new";
              break;
          }

          return (
            <div style={{ display: "flex", height: "20px" }}>
              <StatusBadge className={className} />
              <TransNo>{text}</TransNo>
            </div>
          );
          // return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "date" }),
        dataIndex: "transaction_date",
        rowKey: "transaction_date",
        width: "15%",
        render: text => {
          if (text) {
            return <span>{moment(text).format("DD MMMM YYYY HH:mm:ss")}</span>;
          }
        }
      },
      {
        title: getLanguage({ id: "sidebar.location" }),
        dataIndex: "location.location_name",
        rowKey: "location.location_name",
        width: "30%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "createdBy" }),
        dataIndex: "created_by",
        rowKey: "created_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "requestedBy" }),
        dataIndex: "requested_by",
        rowKey: "requested_by",
        width: "10%",
        render: text => {
          if (text && text.length > 25) {
            return (
              <Tooltip placement="bottom" title={text}>
                {text.substring(0, 25) + "..."}
              </Tooltip>
            );
          } else {
            return text;
          }
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "transaction_status",
        rowKey: "transaction_status",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          switch (text) {
            case 1:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
            case 2:
              className = "requested";
              display = getLanguage({ id: "requested" });
              break;
            case 3:
              className = "progress";
              display = getLanguage({ id: "progress" });
              break;
            case 4:
              className = "fulfilled";
              display = getLanguage({ id: "fulfilled" });
              break;
            case 5:
              className = "cancelled";
              display = getLanguage({ id: "closed" });
              break;
            default:
              className = "new";
              display = getLanguage({ id: "new" });
              break;
          }

          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];

    //console.log(permissions);
    if (permissions.includes("Update Item Request") && columns.length === 6) {
      columns.push(this.listAction);
    }

    Object.keys(itemRequests).map((itemRequest, index) => {
      return dataSource.push({
        ...itemRequests[itemRequest],
        key: itemRequest
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.itemRequest;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="itemRequests.pageHeader" />}
            marginBottom={true}
          >
            {permissions.includes("Create Item Request") ? (
              <Link to={`${this.props.match.path}/add`}>
                <Button type="primary" className="mateAddInvoiceBtn">
                  <IntlMessages id="itemRequests.button.add" />
                </Button>
              </Link>
            ) : null}
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                `${getLanguage({ id: "transactionNo" })}, ` +
                `${getLanguage({ id: "sidebar.location" })}, ` +
                `${getLanguage({ id: "createdBy" })}, ` +
                `${getLanguage({ id: "requestedBy" })}`
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                // width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <div className="isoFilter">
            <Collapse style={customCollapseStyle}>
              <Panel header={<IntlMessages id="filter.header" />} key="1">
                <Row
                  justify="space-between"
                  type="flex"
                  style={{ marginBottom: "10px" }}
                >
                  <Col span={12}>
                    <label>
                      <IntlMessages id="locations.pageHeader" />
                    </label>
                    <br />
                    <Select
                      showSearch
                      onChange={this.handleFilterChange.bind(
                        this,
                        "id_location"
                      )}
                      value={
                        this.state.filter.id_location
                          ? this.state.filter.id_location
                          : []
                      }
                      style={{ width: "95%" }}
                      placeholder={
                        <IntlMessages id="itemRequests.filter.select.location" />
                      }
                      optionFilterProp="children"
                      filterOption={true}
                      loading={loadingForeign}
                      allowClear={true}
                      //showArrow={false}
                    >
                      {locations.map((data, key) => (
                        <Option key={key} value={data.id_location}>
                          {`${data.location_code} (${data.location_name})`}
                        </Option>
                      ))}
                    </Select>
                  </Col>
                  <Col span={12}>
                    <label>
                      <IntlMessages id="itemRequests.filter.date" />
                    </label>
                    <br />
                    <RangePicker
                      format="DD MMMM YYYY HH:mm:ss"
                      showTime
                      allowClear={true}
                      style={{ width: "100%" }}
                      value={
                        this.state.filter.start_date &&
                        this.state.filter.end_date
                          ? [
                              moment(this.state.filter.start_date),
                              moment(this.state.filter.end_date)
                            ]
                          : []
                      }
                      onChange={this.handleFilterChange.bind(this, "date")}
                    />
                  </Col>
                </Row>
                <Row type="flex" justify="start">
                  <Col span={12}>
                    <Row justify="space-between" gutter={1} type="flex">
                      <Col span={8}>
                        <label>
                          <IntlMessages id="status" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "transaction_status"
                          )}
                          value={
                            this.state.filter.transaction_status
                              ? this.state.filter.transaction_status
                              : []
                          }
                          style={{ width: "95%" }}
                          placeholder="Status"
                          optionFilterProp="children"
                          filterOption={true}
                          // loading={this.props.location.loadingFilterSites}
                          allowClear={true}
                          //showArrow={false}
                        >
                          <Option value={1}>
                            {getLanguage({ id: "new" })}
                          </Option>
                          <Option value={2}>
                            {getLanguage({ id: "requested" })}
                          </Option>
                          <Option value={3}>
                            {getLanguage({ id: "progress" })}
                          </Option>
                          <Option value={4}>
                            {getLanguage({ id: "fulfilled" })}
                          </Option>
                          <Option value={5}>
                            {getLanguage({ id: "closed" })}
                          </Option>
                        </Select>
                      </Col>
                      <Col span={8}>
                        <label>
                          <IntlMessages id="dataPerPage" />
                        </label>
                        <br />
                        <Select
                          showSearch
                          onChange={this.handleFilterChange.bind(
                            this,
                            "pagination"
                          )}
                          value={
                            this.state.filter.pagination
                              ? this.state.filter.pagination
                              : page_size
                          }
                          style={{ width: "95%" }}
                          // placeholder={
                          //   <IntlMessages id="pettyCashTypes.filter.select.transactionType" />
                          // }
                          optionFilterProp="children"
                          filterOption={true}
                          //allowClear={true}
                          //showArrow={false}
                        >
                          <Option key={1} value={10}>
                            10
                          </Option>
                          <Option key={2} value={25}>
                            25
                          </Option>
                          <Option key={3} value={50}>
                            50
                          </Option>
                          <Option key={4} value={100}>
                            100
                          </Option>
                        </Select>
                      </Col>
                      <Col
                        span={8}
                        style={{
                          marginTop: "auto"
                        }}
                      >
                        <Button
                          size="small"
                          type="primary"
                          style={{ marginRight: "10px" }}
                          onClick={this.handleFilter}
                        >
                          <IntlMessages id="filter.submit" />
                        </Button>
                        <Button onClick={this.handleResetFilter} size="small">
                          <IntlMessages id="filter.reset" />
                        </Button>
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </Panel>
            </Collapse>
          </div>

          <CardWrapper>
            {itemRequests.length === 0 && !this.props.itemRequest.loading ? (
              <Empty
                description={<IntlMessages id="itemRequests.table.noData" />}
              />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    itemRequest: state.itemRequest,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(ItemRequest);
